import React, { useState } from 'react'
import axios from 'axios'
import './Register.css'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import Alert from '@mui/material/Alert'
import { Typography } from '@mui/material'
import dummy from '../assets/avatar.png'


const Register = () => {

    const [username, setUsername] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const history = useHistory()
    const [error, setError] = useState(false)
    const [image, setImage] = useState()
    const [file, setFile] = useState()


    const handleSubmit = async (e) => {

        e.preventDefault()

        if (username === '') {
            setError(true)
            return
        }

        if (password !== confirmPassword) {
            setError(true)
            return
        }

        const myForm = new FormData()
        myForm.append('username', username)
        myForm.append('email', email)
        myForm.append('password', password)
        myForm.append('confirmPassword', confirmPassword)
        myForm.append('image', file)

        try {
            // const user = await axios.post('/api/register', { username, email, password, confirmPassword })
            const user = await axios.post('/api/register', myForm, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                }
            })
            history.push('/login')
            console.log(user)
        } catch (error) {
            console.log(error)
        }
    }


    const imageUploadHandler = (fileData) => {
        setFile(fileData)
        const reader = new FileReader()
        reader.readAsDataURL(fileData)
        reader.onload = () => {
            if (reader.readyState === 2) {
                setImage(reader.result)
            }
        }
    }


    return (
        <div>
            {error &&
                <Alert
                    severity="error"
                    variant='fill' color='warning'
                    sx={{ bgcolor: '#eb8038', mb: 2 }}
                    onClose={() => { setError(false) }}
                >
                    {
                        confirmPassword === password ?
                            <Typography style={{ color: '#ffffff' }}> Fill all required fields!!!!</Typography>
                            : <Typography style={{ color: '#ffffff' }}> Password Don't Match</Typography>
                    }
                </Alert>
            }
            <div className="form-container">
                <form className="register-form" onSubmit={handleSubmit}>


                    <input
                        className="form-field"
                        type="text"
                        placeholder="Username"
                        name="username"
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                    />

                    <input
                        className="form-field"
                        type="email"
                        placeholder="Email"
                        name="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                    />

                    <input
                        className="form-field"
                        type="password"
                        placeholder="Password"
                        name="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                    />

                    <input
                        className="form-field"
                        type="password"
                        placeholder="Confirm Password"
                        name="confirmPassword"
                        value={confirmPassword}
                        onChange={(e) => setConfirmPassword(e.target.value)}
                    />
                    <div className="form-field" style={{ display: 'flex', height: '10vh' }}>
                        <label htmlFor='imageInput' className='uploader'>Profile pic</label>
                        <input
                            type='file'
                            name="image"
                            id="imageInput"
                            accept='image/*'
                            placeholder="Upload"
                            style={{ color: 'tomato', width: '30%', height: '50%', marginRight: '30%', display: 'none' }}
                            onChange={(e) => imageUploadHandler(e.target.files[0])}
                        />
                        <img src={image ? image : dummy} alt='profile' style={{ width: '8vh', height: '8vh', borderRadius: '50%' }} />
                    </div>
                    <button className="form-field" type="submit">
                        Register
                    </button>
                </form>
            </div>
        </div>

    )
}

export default Register
