import React from 'react'
import { useState } from 'react'
import axios from 'axios'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import { useGoogleLogin } from '@react-oauth/google'
import Alert from '@mui/material/Alert'
import { Typography } from '@mui/material'
import GoogleIcon from '@mui/icons-material/Google'
import { CircularProgress } from '@mui/material'



const Login = () => {

    const history = useHistory()
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [alert, setAlert] = useState(false)
    const [alertMessage, setAlertMessage] = useState('')
    const [progress, setProgress] = useState(false)




    const handleSubmit = async (e) => {
        e.preventDefault()
        try {
            setProgress(true)
            const { data } = await axios.post('/api/login', { username, password })
            history.push('/secure')
        } catch (error) {
            setAlert(true)
            setAlertMessage(error.response.data)
            setUsername('')
            setPassword('')
            console.log(error)
        }

        setProgress(false)
    }


    const login = useGoogleLogin({
        onSuccess: tokenResponse => tokenHandler(tokenResponse.access_token)
    })

    const tokenHandler = async (accessToken) => {
        try {
            const { data } = await axios.post("/api/token/handle", { accessToken })
            console.log(data)
            history.push('/secure')
        } catch (error) {
            setAlertMessage(error.response.data)
            setAlert(true)
            setUsername('')
            setPassword('')
        }
    }


    return (

        <div className="form-container" >
            {progress ? <CircularProgress style={{ marginLeft: '40%' }} />
                :
                <>
                    {alert &&
                        <Alert
                            severity="error"
                            variant='fill' color='warning'
                            sx={{ bgcolor: '#eb8038', mb: 2 }}
                            onClose={() => { setAlert(false) }}
                        >
                            <Typography style={{ color: '#ffffff' }}>{alertMessage}</Typography>
                        </Alert>
                    }
                    <form className="register-form" onSubmit={handleSubmit}>


                        <input
                            className="form-field"
                            type="text"
                            placeholder="Username"
                            name="username"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                        />



                        <input
                            className="form-field"
                            type="password"
                            placeholder="Password"
                            name="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                        />

                        <button className="form-field" type="submit">
                            Login
                        </button>

                    </form>
                    <div style={{ display: 'flex', justifyContent: 'center', padding: '5px 10px', alignItems: 'center' }}>
                        <button
                            className="form-field"
                            onClick={() => login()}
                            style={{ borderRadius: '5%', backgroundColor: '#3268a8' }}
                        >
                            <div style={{ display: 'flex', alignItems: 'center' }}>Login with  <GoogleIcon style={{ marginLeft: '7px' }} /></div>
                        </button>
                    </div>
                    <div className='bottom-text'
                        style={{
                            display: 'flex',
                            justifyContent: 'flex-end'
                        }}
                    >
                        <p
                            style={{
                                margin: '2px 2px',
                                color: 'tomato',
                                cursor: 'pointer'
                            }}
                            onClick={() => history.push('/forgotPassword')}
                        >Forgot password</p></div>

                </>
            }
        </div>

    )

}

export default Login
