import React, { useState } from 'react'
import Alert from '@mui/material/Alert'
import { Typography } from '@mui/material'
import axios from 'axios'
// import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import '@fontsource/roboto/300.css'
import '@fontsource/roboto/400.css'
import '@fontsource/roboto/500.css'
import '@fontsource/roboto/700.css'
import { CircularProgress } from '@mui/material'



const ForgotPassword = () => {

    const [email, setEmail] = useState('')
    const [error, setError] = useState('')
    const [alertMessage, setAlertMessage] = useState('')
    const [success, setSuccess] = useState(false)
    // const history = useHistory()
    const [progress, setProgress] = useState(false)


    const handleSubmit = async (e) => {
        e.preventDefault()
        setProgress(true)
        try {
            const { data } = await axios.post("/api/forgotPassword", { email })
            setSuccess(true)
            setAlertMessage(data)
            setProgress(false)
        } catch (error) {
            setError(true)
            setAlertMessage(error.response.data)
            setProgress(false)
        }
    }


    return (
        <div>
            {success ?
                <Alert
                    severity="info"
                    variant='filled'
                    sx={{ mb: 2, width: '30vw' }}
                >
                    <Typography variant='h6' style={{ color: '#ffffff' }}> {alertMessage}</Typography>
                </Alert>
                :
                <div>
                    {error &&
                        <Alert
                            severity="error"
                            variant='filled' color='warning'
                            sx={{ bgcolor: '#eb8038', mb: 2 }}
                            onClose={() => {
                                setError(false)
                                setAlertMessage('')
                            }}
                        >
                            <Typography style={{ color: '#ffffff' }}> {alertMessage}</Typography>
                        </Alert>
                    }

                    <div className="form-container">
                        {progress ?
                            <div style={{ display: 'flex', justifyContent: 'center' }}><CircularProgress sx={{ width: '100%' }} /></div>
                            :
                            <form className="register-form" onSubmit={handleSubmit}>
                                <input
                                    className="form-field"
                                    type="email"
                                    placeholder="Email"
                                    name="email"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                />
                                <button className="form-field" type="submit">
                                    Send the Link
                                </button>
                            </form>
                        }
                    </div>
                </div>
            }
        </div>
    )
}

export default ForgotPassword
