import React, { useEffect, useState } from 'react'
import './SecurePage.css'
import axios from 'axios'
import { useHistory } from 'react-router-dom'
import LogoutIcon from '@mui/icons-material/Logout'
import Grid from '@mui/material/Grid'
import Rating from '@mui/material/Rating'
import Pagination from '@mui/material/Pagination'
import { Button } from '@mui/material'
import { CircularProgress } from '@mui/material'
import { Slider } from '@mui/material'


const SecurePage = () => {


    const history = useHistory()
    const [login, setLogin] = useState(false)
    const [progress, setProgress] = useState(true)
    const [products, setProducts] = useState([])
    const [page, SetPage] = useState(1)
    const [totalpages, setTotalPages] = useState(0)
    const [productProgress, setProductProgress] = useState(false)
    const [values, setValues] = useState([0, 5])



    const logoutHandler = async () => {

        await axios.get('/api/logout')
        history.push("/")
    }

    const handleChange = (e, v) => {
        setValues(v)
        SetPage(1)
    }



    useEffect(() => {

        const checkForLogin = async () => {
            try {
                const { data } = await axios.get('/api/checkLogin')
                setLogin(data)
                if (data === false) history.push('/login')
                const { data: count } = await axios.get(`/api/product/count?lb=${values[0]}&hb=${values[1]}`)
                console.log(count, 'count')
                setTotalPages(Math.ceil(count / 3))
            } catch (error) {
                console.log(error)
            }
        }

        setProgress(true)
        checkForLogin()
        setProgress(false)

    }, [history, values])

    useEffect(() => {
        if (!login) return

        const getProductsInfo = async () => {
            try {
                setProductProgress(true)
                const { data } = await axios.get(`/api/product/info?page=${page}&lb=${values[0]}&hb=${values[1]}`)
                setProducts(data)
                setProductProgress(false)
                console.log(data)
            } catch (error) {
                console.log(error)
            }
        }

        getProductsInfo()

    }, [login, history, page, values])



    return (
        <div >
            {progress ?

                <CircularProgress />
                :
                <div className='page'>
                    <div className='navbar'>
                        <div className='logout-btn'>
                            <Button className='logout-btn' variant='contained' onClick={logoutHandler}>
                                <LogoutIcon />
                            </Button></div>
                    </div>
                    <div className='content'>
                        <div className='sidebar'>
                            <div className='slider-box'>
                                <Slider
                                    getAriaLabel={() => 'Temperature range'}
                                    value={values}
                                    max={5}
                                    min={0}
                                    step={1}
                                    onChange={handleChange}
                                    valueLabelDisplay="auto"
                                // getAriaValueText={}
                                />
                            </div>
                        </div>
                        <div className='main-content'>
                            <div className='product-grid'>
                                <Grid container spacing={3}>
                                    {products && products.map((product, index) =>
                                        <>
                                            {productProgress ?
                                                <Grid item xs={4} key={index}>
                                                    <div className='product-page'>
                                                        <CircularProgress style={{ position: 'absolute', top: '50%', left: '50%' }} />
                                                    </div>
                                                </Grid>

                                                :
                                                <Grid item xs={4} key={index}>
                                                    <div className='product-page'>
                                                        <div className='wrapper'>
                                                            <div className='image-container'>
                                                                <img src={product.image} alt='pic' />
                                                            </div>
                                                            <div className='title'>
                                                                {product?.title}
                                                            </div>
                                                            <div className='price'>
                                                                $ {Number(product?.price).toFixed(2)}
                                                            </div>
                                                            <div className='rating'>
                                                                <Rating name="read-only"
                                                                    size='large'
                                                                    value={Math.floor(Number(product?.rating))}
                                                                    readOnly />
                                                            </div>
                                                        </div>
                                                    </div>
                                                </Grid>
                                            }
                                        </>
                                    )}
                                </Grid>
                            </div>
                            <div className='pagination-tool'>
                                {totalpages && <Pagination  onChange={(e, p) => SetPage(p)}  count={totalpages} color="secondary" />}
                            </div>
                        </div>
                    </div>
                </div>
            }
        </div>
    )
}



export default SecurePage
