import React from 'react'
import './App.css'
import { Switch } from 'react-router-dom'
import { Route } from 'react-router-dom'
import Login from '../components/Login.js'
import Register from '../components/Register.js'
import { Link } from 'react-router-dom/cjs/react-router-dom.min.js'
import SecurePage from './SecurePage.js'
import ForgotPassword from '../components/ForgotPassword.js'
import ResetPassword from '../components/ResetPassword.js'



const App = () => {



    return (
        <>
            <Switch>
                <Route exact path='/'>
                    <ul>
                        <li><Link to='/register'><h1>Register</h1></Link></li>
                        <li><Link to='/login'><h1>Login</h1></Link></li>
                        <li><Link to='/secure'><h1>Secure Page</h1></Link></li>
                    </ul>

                </Route>
                <Route exact path='/register'><Register /></Route>
                <Route exact path='/login'><Login /></Route>
                <Route exact path='/secure'><SecurePage /></Route>
                <Route exact path='/forgotPassword'><ForgotPassword /></Route>
                <Route exact path='/resetPassword/:token/:email'><ResetPassword /></Route>
            </Switch>
        </>
    )
}

export default App
