import React, { useState } from 'react'
import Alert from '@mui/material/Alert'
import { Typography } from '@mui/material'
import axios from 'axios'
import { useHistory, useParams } from 'react-router-dom/cjs/react-router-dom.min'
import { CircularProgress } from '@mui/material'



const ResetPassword = () => {

    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const { token, email } = useParams()
    const [alert, setAlert] = useState(false)
    const [alertMessage, setAlertMessage] = useState('')
    const history = useHistory()
    const [progress, setProgress] = useState(false)


    const handleSubmit = async (e) => {
        e.preventDefault()
        if (password !== confirmPassword) {
            setAlert(true)
            setAlertMessage("Passwords Don't Match")
            return
        }

        setProgress(true)

        try {
            await axios.post('/api/resetPassword', { token, email, password })
            history.push('/login')
        } catch (error) {
            setAlert(true)
            setAlertMessage(error.response.data)
            setProgress(false)
        }

    }



    return (
        <div>
            {alert &&
                <Alert
                    severity="error"
                    variant='fill' color='warning'
                    sx={{ bgcolor: '#eb8038', mb: 2 }}
                    onClose={() => { setAlert(false) }}
                >
                    <Typography style={{ color: '#ffffff' }}>{alertMessage}</Typography>
                </Alert>
            }

            <div className="form-container">
                {progress ?
                    <div style={{ display: 'flex', justifyContent: 'center' }}><CircularProgress sx={{ width: '100%' }} /></div>
                    :
                    <form className="register-form" onSubmit={handleSubmit}>
                        <input
                            className="form-field"
                            type="password"
                            placeholder="Password"
                            name="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                        />

                        <input
                            className="form-field"
                            type="password"
                            placeholder="Confirm Password"
                            name="confirmPassword"
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                        />
                        <button className="form-field" type="submit">
                            Submit
                        </button>
                    </form>}
            </div>
        </div>
    )
}

export default ResetPassword
